.switch {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin: 40px 0 20px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.switch__btn {
    margin-right: 10px;
    margin-bottom: 10px;
    width: 70px;
    -ms-flex-negative: 0;
        flex-shrink: 0;
}
.books-row {
    margin-right: -0.75rem;
    margin-left: -0.75rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.yr-hidden,
.title-hidden {
    opacity: 0;
}
.yr-shown,
.title-shown {
    opacity: 1;
}
.yr-shown-active,
.title-shown-active {
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.book-hidden {
    opacity: 0;
}
.book-shown {
    opacity: 1;
}
.book-shown-active {
    -webkit-transition: all 0.7s ease;
    transition: all 0.7s ease;
}
.book {
    margin-bottom: 25px;
}
.book.col.xl4 {
    margin-left: 0;
}
.book .book__title {
    margin-top: 0;
    font-weight: 500;
}
.book__description {
    font-size: 17px;
}
.book__author {
    margin: 0;
}
.book__year {
    margin: 0;
}
.book__delete {
    padding: 0;
    background-color: transparent;
    border: none;
    padding: 10px;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    position: absolute;
    right: 0;
}
.book__delete:hover,
.book__delete:focus {
    background-color: transparent;
    opacity: 0.7;
}
.book__read-year {
    margin: 5px 0 0;
    font-size: 13px;
}
.btn-floating {
  position: absolute;
  right: 10px;
  bottom: 10px;
}
.card {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.card .card-content {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}

